import axios from 'axios';
import Vue from 'vue';
import router from './router.js';
import store from './vuex.js';
import * as types from '../store/types.js';
let loadingId = null;
//
axios.interceptors.request.use(function(config) {
  //config   axios(config)
  
  // token
  let token = window.localStorage.getItem('user')
  token = token ? JSON.parse(window.localStorage.getItem('user')).token : ''
  config.headers={
    token: token
  }
  
  // //loading
 clearTimeout(loadingId);
 loadingId = setTimeout(()=>store.commit(types.UPDATE_LOADING,true),200)
  
  return config;// 
}, function(error) {
  // 
  return Promise.reject(error);
});

// 
axios.interceptors.response.use(function(response) {
	 clearTimeout(loadingId);
  //response  ~~ axiosres
  //    res.status   res.data.err == 2
  //tokenlogin,
  let currentRoute = router.currentRoute.fullPath;//string
  if(response.data.err===2 && !currentRoute.includes('/login')){
    
    router.replace({
      path:'/login',
      query:{p:currentRoute}
    })
  }
  
 store.commit(types.UPDATE_LOADING,false)
  
  return response;//
}, function(error) {
  // 
  return Promise.reject(error);
});


Vue.prototype.$axios=axios;
// window.axios=axios;  
export default axios;